@import '../variables';

.header {
  border-bottom: 1px solid $gray-line;
  padding: 40px 0;

  .container {
    display: flex;
    align-items: center;
    justify-content: space-between;
    @media (max-width: 590px) {
      flex-direction: column;
      text-align: center;
    }
  }

  &__logo {
    display: flex;
    @media (max-width: 590px) {
      flex-direction: column;
    }
  }

  img {
    margin-right: 15px;
    @media (max-width: 590px) {
      margin: 0 auto 10px;
    }
  }

  h1 {
    color: #181818;
    font-size: 24px;
    letter-spacing: 1%;
    text-transform: uppercase;
    font-weight: 800;
    word-wrap: break-word;
  }

  p {
    color: #7b7b7b;
    word-wrap: break-word;
    @media (max-width: 590px) {
      margin-bottom: 10px;
    }
  }
}
