.root {
  text-align: center;
  margin: 50px;
  h1::after {
    content: '404';
    font-size: 10rem;
    font-weight: 700;
  }
  button {
    margin-top: 20px;
    padding: 10px 20px;
    border: none;
    background: #000;
    color: #fff;
    font-size: 1.2rem;
    font-weight: 700;
    cursor: pointer;
    transition: all 0.3s ease-in-out;
    &:hover {
      background: #fff;
      color: #000;
    }
  }
  h2::after {
    content: 'ðŸ¦—';
    display: block;
    padding: 4rem 0;
    font-size: 10rem;
    font-weight: 700;
  }
  span {
    display: block;
    font-size: 1.2rem;
    font-weight: 900;
  }
}
