.root {
  position: relative;

  .input {
    border: 1px solid rgba(0, 0, 0, 0.1);
    padding: 12px 20px 12px 42px;
    width: 300px;
    border-radius: 10px;
    font-size: 16px;
    transition: all 0.3s ease-in-out;
    &:focus {
      border: 1px solid rgba(0, 0, 0, 0.4);
      width: 400px;
    }
    @media (max-width: 920px) {
      width: 50%;
      margin-bottom: 10px;
      padding: 12px 20px 12px 20px;

      &:focus {
        border: 1px solid rgba(0, 0, 0, 0.4);
        width: 100%;
      }
    }
  }

  .icon {
    width: 22px;
    height: 22px;
    opacity: 0.3;
    position: absolute;
    left: 14px;
    top: 12px;
    @media (max-width: 920px) {
      display: none;
    }
  }
  .iconClear {
    width: 22px;
    height: 22px;
    opacity: 0.3;
    position: absolute;
    right: 14px;
    top: 12px;
    @media (max-width: 920px) {
      display: none;
    }
    &:hover {
      cursor: pointer;
      opacity: 0.8;
    }
  }
}
