.root {
  display: block;
  width: fit-content;
  margin: 30px auto 0;
  li {
    display: inline-flex;
    a {
      border: 1px solid #fe5f1e;
      display: inline-flex;
      border-radius: 30px;
      width: 45px;
      height: 45px;
      justify-content: center;
      align-items: center;
      margin-right: 10px;
      cursor: pointer;

      &:hover {
        background-color: #fe5f1e;
        color: #fff;
      }
      color: #fe5f1e;
    }
  }
  :global {
    .selected {
      a {
        background-color: #fe5f1e;
        color: #fff;
      }
    }
  }
}
